import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { Suspense, lazy, useState } from "react";
import { useTranslation } from "react-i18next";
import DropStudentAdmissionFeesCard from "./DropStudentAdmissionFeesCard";
import UpdateStudentAdmissionFeesCardStructure from "../UpdateStudentAdmissionFeesCardStructure";
import { useLocation } from "react-router-dom";
import QvipleLoader from "../../../Loader/QvipleLoader";
import NotifyEvent from "../../../Utils/Snackbar/NotifyEvent";
const AdmissionProfileCardCancel = lazy(() =>
  import("../../AdmissionManager/AdmissionCancelled/AdmissionProfileCardCancel")
);

const StudentAdmissionFeesCardMenu = ({
  onClose,
  rid,
  sid,
  is_drop,
  structureId,
  aid,
  appId,
  did,
  bid,
  cmid,
  isOneInstallment,
  admission,
}) => {
  const { t } = useTranslation();
  const [drop, setDrop] = useState(false);
  const [feeEdit, setFeeEdit] = useState(false);
  const [step, setStep] = useState("");
  const getQuery = useLocation();
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });

  const onFeeEdit = () => {
    if (isOneInstallment) {
      setFeeEdit((pre) => !pre);
    } else {
      setSnackbar({
        msg: "Only fees structures with one installment can be edited.",
        open: true,
        variant: "error",
      });
    }
    // setFeeEdit((pre) => !pre);
  };
  const onDropToggle = () => {
    setDrop((pre) => !pre);
  };
  const onStepClose = () => {
    setStep("");
  };

  return (
    <>
      {drop ? (
        <DropStudentAdmissionFeesCard
          rid={rid}
          sid={sid}
          onClose={onDropToggle}
          onParentClose={onClose}
        />
      ) : feeEdit ? (
        <UpdateStudentAdmissionFeesCardStructure
          structureId={structureId}
          onClose={onFeeEdit}
          aid={aid}
          appId={appId}
          sid={sid}
          rid={rid}
          did={did}
          bid={bid}
          cmid={cmid}
          onParentClose={onClose}
        />
      ) : step === "CANCEL" ? (
        <Suspense fallback={<QvipleLoader />}>
          <AdmissionProfileCardCancel
            onClose={onStepClose}
            appId={appId}
            admission={admission}
            onParentClose={onClose}
          />
        </Suspense>
      ) : (
        <ModalMenu onClose={onClose}>
          <h6 onClick={onFeeEdit}>{t("edit_fees_structure")}</h6>
          {is_drop ? <h6 onClick={onDropToggle}>{t("drop")}</h6> : null}
          {getQuery.state?.cancelAccess ? (
            <h6 onClick={() => setStep("CANCEL")}>{t("cancel_addmission")}</h6>
          ) : null}
        </ModalMenu>
      )}
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default StudentAdmissionFeesCardMenu;
