import { useState } from "react";
import { useStudentAdmissionFeeCardPaidInstallmentRemove } from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";

const StudentAdmissionFeesCardInstallmentCardGovToApplicableMenu = ({
  onClose,
  onMenu,
  install,
  admission,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [studentAdmissionFeeCardPaidInstallmentRemove] =
    useStudentAdmissionFeeCardPaidInstallmentRemove();
  const onInstallmentRemove = () => {
    if (
      admission?.government_card?._id &&
      install?._id &&
      admission?.appId?.admissionAdmin?.institute?.financeDepart?.[0]
    ) {
      setDisabled((pre) => !pre);
      studentAdmissionFeeCardPaidInstallmentRemove({
        rid: install?._id,
        nid: admission?.government_card?._id,
        bodyParameter: {
          fid: admission?.appId?.admissionAdmin?.institute?.financeDepart?.[0],
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        {install?.status !== "Paid" &&
        install?.revert_status !==
          "Government Fees Shifted To Applicable Fees" ? (
          <h6 onClick={onMenu}>{t("transfer_to_applicable_fees")}</h6>
        ) : null}
        {install?.status === "Paid" ? (
          <h6 onClick={onInstallmentRemove}>{t("remove_installment")}</h6>
        ) : null}
      </ModalMenu>
    </>
  );
};

export default StudentAdmissionFeesCardInstallmentCardGovToApplicableMenu;
