import { useState } from "react";
import style from "./StudentAdmissionFees.module.css";
import dayjs from "dayjs";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";
import StudentAdmissionFeesCardInstallmentCardMenu from "./Menus/StudentAdmissionFeesCardInstallmentCardMenu";
import StudentAdmissionFeesCardReceipt from "../TransactionReceipt/Admission/StudentAdmissionFeesCardReceipt";
import StudentAdmissionCollectFees from "./CollectFees/StudentAdmissionCollectFees";
import StudentAdmissionFeesCardInstallmentCardTransfer from "./Menus/StudentAdmissionFeesCardInstallmentCardTransfer";
import StudentMultipleInstallmentFragment from "./Menus/StudentMultipleInstallmentFragment";
import StudentAdmissionCollectFeesType from "./CollectFees/StudentAdmissionCollectFeesType";
const StudentAdmissionFeesCardInstallmentCard = ({ install, admission }) => {
  const getQuery = useLocation();

  const { t } = useTranslation();
  const [step, setStep] = useState("");
  const onCloseStep = () => {
    setStep("");
  };
  const onEditMenu = () => {
    setStep(1);
  };
  const onEditMenu1 = () => {
    setStep(3);
  };

  const onCollectFees = () => {
    // if (install?.isEnable)
    if (install?.isEnable && buttonAccess[getQuery.state?.collectAccess]) {
      if (admission?.setOffPrice > 0) {
        setStep(5);
      } else {
        setStep(2);
      }
    }
  };

  const onMultipleInstallment = () => {
    setStep(4);
  };

  // console.info("dfgfdgfd", getQuery.state);
  return (
    <>
      <div
        className={style.student_admission_cash_card}
        style={{
          position: "relative",
        }}
      >
        <section className={style.admission_fees_amount}>
          <h6>
            {install?.installmentValue}{" "}
            {install?.status === "Paid" && (
              <span>
                {dayjs(install?.fee_receipt?.fee_transaction_date).format(
                  "DD-MMM-YY"
                )}
              </span>
            )}
          </h6>
          <h6>
            {t("rs_only")}
            {install?.remainAmount === 0
              ? install?.set_off
              : install?.remainAmount ?? 0}
          </h6>
        </section>
        {install?.remainAmount === 0 ? null : (
          <section className={style.admission_fees_grant}>
            {install?.status === "Paid" &&
            install?.receipt_status !== "Rejected" ? (
              <>
                <h6>
                  {install?.fee_receipt?.fee_payment_mode === "By Cash"
                    ? "Offline"
                    : install?.fee_receipt?.fee_payment_mode
                    ? install?.fee_receipt?.fee_payment_mode
                    : install?.refund_status ?? ""}
                </h6>
                <h6>
                  {install?.fee_receipt?.set_off_status === "Set Off"
                    ? "SetOff"
                    : install?.fee_receipt?.fee_payment_mode === "By Cash"
                    ? "Cash"
                    : install?.fee_receipt?.fee_payment_mode ===
                      "Exempted/Unrecovered"
                    ? "N/A"
                    : "Bank"}
                </h6>
              </>
            ) : (
              <>
                {install?.receipt_status === "Rejected" ? (
                  <h6
                    style={{
                      margin: "0",
                    }}
                  >
                    {`${t("reason")} : `}
                    {install?.reason}
                  </h6>
                ) : (
                  <h6
                    style={{
                      margin: "0",
                    }}
                  >
                    {t("due_on")} {` `} {install?.dueDate}
                  </h6>
                )}
              </>
            )}
          </section>
        )}

        {install?.component?.gov ? (
          <>
            <section className={style.admission_fees_grant}>
              <h6
                style={{
                  margin: "0",
                }}
              >
                {t("applicable_fees")}
              </h6>
              <h6
                style={{
                  margin: "0",
                  fontSize: "13px",
                  color: "#121212",
                }}
              >
                {t("rs_only")}
                {install?.component?.app ?? 0}
              </h6>
            </section>
            <section className={style.admission_fees_grant}>
              <h6
                style={{
                  margin: "0",
                }}
              >
                {t("govt_fees")}
              </h6>
              <h6
                style={{
                  margin: "0",
                  fontSize: "13px",
                  color: "#121212",
                }}
              >
                {t("rs_only")}
                {install?.component?.gov ?? 0}
              </h6>
            </section>
          </>
        ) : null}

        {install?.set_off_message ? (
          <>
            <section className={style.admission_fees_grant}>
              <h6
                style={{
                  margin: "0",
                  marginBottom: "0.2rem",
                }}
              >
                {install?.set_off_message}
              </h6>
            </section>
          </>
        ) : null}
        {
          // multipleInstall[getQuery.state?.collectAccess] ||
          // multipleInstall[getQuery.state?.otherFlow]
          editInstallmentControl[getQuery.state?.editInstallmentControl] ? (
            // for remove installment
            //  &&
            // install?.status !== "Paid"
            <img
              src={`${assestsUserFinanceUrl}/menu.svg`}
              alt="menu icon"
              onClick={onEditMenu}
              className={style.edit_icon}
              title="Menu"
              style={{
                cursor: "pointer",
                top: "1%",
                right: "0.5%",
              }}
            />
          ) : null
        }

        {install?.receipt_status !== "Requested" &&
        install?.receipt_status !== "Rejected" &&
        install?.status === "Paid" ? (
          install?.cover_status ? (
            <button
              className={style.student_admission_collect_btn}
              style={{
                width: "100%",
                backgroundColor: "rgb(92 92 92)",
                fontSize: "0.7rem",
              }}
            >
              {install?.cover_status}
            </button>
          ) : install?.remainAmount === 0 ? null : (
            <>
              {install?.receipt_status === "Setoff" ||
              install?.fee_receipt?.set_off_status === "Set Off" ? null : (
                <StudentAdmissionFeesCardReceipt
                  rid={install?.fee_receipt?._id}
                  appId={admission?.appId?._id}
                  receiptFile={install?.fee_receipt?.receipt_file}
                />
              )}
            </>
          )
        ) : (
          <button
            className={style.student_admission_collect_btn}
            style={
              // install?.receipt_status === "Requested"
              //   ? {
              //       width: "100%",
              //       backgroundColor: "#B8B83E",
              //     }
              //   : install?.receipt_status === "Rejected"
              //   ? {
              //       width: "100%",
              //       backgroundColor: "#B8B83E",
              //     }
              //   :
              {
                width: "100%",
              }
            }
            onClick={onCollectFees}
          >
            {install?.receipt_status === "Requested"
              ? t("receipt_requested")
              : install?.receipt_status === "Rejected"
              ? t("receipt_rejected")
              : t(
                  buttonAccess[getQuery.state?.collectAccess]
                    ? "collect_fees"
                    : "unpaid"
                )}
          </button>
        )}
      </div>
      {step === 1 ? (
        <StudentAdmissionFeesCardInstallmentCardMenu
          onClose={onCloseStep}
          rid={admission?._id}
          installId={install?._id}
          onMultipleInstallment={onMultipleInstallment}
          isDelete={install?.remainAmount < 0 ? true : false}
          admission={admission}
          onEditMenu1={onEditMenu1}
          install={install}
        />
      ) : step === 2 ? (
        <StudentAdmissionCollectFees
          onClose={onCloseStep}
          install={install}
          admission={admission}
        />
      ) : step === 3 ? (
        <StudentAdmissionFeesCardInstallmentCardTransfer
          onClose={onCloseStep}
          rid={admission?._id}
          installId={install?._id}
          installAmount={install?.remainAmount}
          onMultipleInstallment={onMultipleInstallment}
        />
      ) : step === 4 ? (
        <StudentMultipleInstallmentFragment
          onClose={onCloseStep}
          pid={admission?.applicable_card?._id}
          install={install}
        />
      ) : step === 5 ? (
        <StudentAdmissionCollectFeesType
          onClose={onCloseStep}
          admission={admission}
          install={install}
        />
      ) : null}
    </>
  );
};

export default StudentAdmissionFeesCardInstallmentCard;

const buttonAccess = {
  FINANCE_MANAGER: true,
  ADMISSION_ADMIN: true,
};

const editInstallmentControl = {
  authorized: true,
};

