import { useState } from "react";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import {
  useStudentAdmissionFeeCardInstallmentRemove,
  useStudentAdmissionFeeCardPaidInstallmentRemove,
} from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import { useTranslation } from "react-i18next";
// import { useLocation } from "react-router-dom";

const StudentAdmissionFeesCardInstallmentCardMenu = ({
  installId,
  onClose,
  rid,
  onMultipleInstallment,
  install,
  admission,
  onEditMenu1,
}) => {
  const { t } = useTranslation();
  // const getQuery = useLocation();

  const [disabled, setDisabled] = useState(false);

  const [studentAdmissionFeeCardInstallmentRemove] =
    useStudentAdmissionFeeCardInstallmentRemove();
  const [studentAdmissionFeeCardPaidInstallmentRemove] =
    useStudentAdmissionFeeCardPaidInstallmentRemove();
  const onRemove = () => {
    if (rid && installId) {
      setDisabled((pre) => !pre);
      studentAdmissionFeeCardInstallmentRemove({
        rid: rid,
        raid: installId,
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onInstallmentRemove = () => {
    if (
      admission?.applicable_card?._id &&
      installId &&
      admission?.appId?.admissionAdmin?.institute?.financeDepart?.[0]
    ) {
      setDisabled((pre) => !pre);
      studentAdmissionFeeCardPaidInstallmentRemove({
        rid: installId,
        nid: admission?.applicable_card?._id,
        bodyParameter: {
          fid: admission?.appId?.admissionAdmin?.institute?.financeDepart?.[0],
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  // console.info("install", admission);
  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        {install?.status !== "Paid" ? (
          <h6 onClick={onMultipleInstallment}>{t("new_installment")}</h6>
        ) : null}
        {install?.status !== "Paid" &&
        install?.remainAmount > 0 &&
        (admission?.government_card?.applicable_fee > 0 ||
          admission?.government_card?.paid_fee > 0 ||
          admission?.government_card?.remaining_fee > 0) ? (
          <h6 onClick={onEditMenu1}>{t("transfer_to_scholarship_fees")}</h6>
        ) : null}
        {install?.remainAmount < 0 ? (
          <h6 onClick={onRemove}>{t("delete")}</h6>
        ) : null}
        {install?.status === "Paid" ? (
          <h6 onClick={onInstallmentRemove}>{t("remove_installment")}</h6>
        ) : null}
      </ModalMenu>
    </>
  );
};

export default StudentAdmissionFeesCardInstallmentCardMenu;

