import { useTranslation } from "react-i18next";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/Income/Income.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useState } from "react";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
import FormInput from "../../../Ui/Input/FormInput";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import {
  slashDateFormat,
  yearMonthDay,
} from "../../../Utils/Functions/dateUtility";
import PaymentModeType from "../../PaymentModeType/PaymentModeType";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import { useStudentAdmissionFeeCardCollect } from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import { paymentCollectType } from "../../../Constant/StudentProfile/student_profile_constant";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../Ui/Tab/Tile/TileCard";
const StudentAdmissionCollectFees = ({ onClose, install, admission }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    amount: "",
    mode: "",
    fee_payment_mode: "",
    fee_payment_amount: "",
    transaction_date: "",
    fee_bank_name: "",
    fee_bank_holder: "",
    fee_utr_reference: "",
    pay_remain: false,
  });
  const [errorField, setErrorField] = useState("");
  const [totalAmount, setTotalAmount] = useState(0);

  const [studentAdmissionFeeCardCollect] = useStudentAdmissionFeeCardCollect();
  const onInputAmount = (e) => {
    setState((pre) => ({
      ...pre,
      amount: +e.target.value,
      fee_payment_amount: +e.target.value,
    }));
  };

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTransactionDate = (val) => {
    setState((prev) => ({
      ...prev,
      transaction_date: slashDateFormat(val),
    }));
  };

  const onModeChange = (val) => {
    setState((prev) => ({
      ...prev,
      mode: "Online",
      fee_payment_mode: val,
    }));
  };
  const onCollectType = (val) => {
    setState((prev) => ({
      ...prev,
      pay_remain: val,
    }));
    if (val) {
      let t = 0;
      for (let rm of admission?.applicable_card?.remaining_array)
        if (rm?.status !== "Paid") t += rm?.remainAmount;
      setTotalAmount(t);
    }
  };
  const onCollectFees = () => {
    let validation = "";
    if (state.fee_payment_mode === "Unrecovered/Exemption")
      validation = requiredField(state, [
        "amount",
        "mode",
        "fee_payment_amount",
        "fee_bank_name",
        "fee_bank_holder",
        "fee_utr_reference",
        "pay_remain",
      ]);
    else
      validation = requiredField(state, [
        "mode",
        "fee_payment_amount",
        "fee_bank_name",
        "fee_bank_holder",
        "fee_utr_reference",
        "pay_remain",
      ]);
    if (existingRequiredField(validation)) {
      if (state.pay_remain && state.amount < totalAmount) {
        setErrorField({
          rest_issue: "Amount should not be less than total remaining fees",
        });
      } else {
        if (
          !state.pay_remain &&
          state.amount > install?.remainAmount &&
          +admission?.fee_structure?.total_installments > 1
        ) {
          setErrorField({
            rest_issue: "Amount should not be greater than remaining fees",
          });
        } else {
          if (admission?.student?._id && install?.appId && install?._id) {
            setDisabled((pre) => !pre);
            studentAdmissionFeeCardCollect({
              aid: admission?.appId?.admissionAdmin?._id,
              sid: admission?.student?._id,
              appId: install?.appId,
              payment: {
                card_id: admission?.applicable_card?._id,
                rid: admission?._id,
                type: install?.installmentValue,
                raid: install?._id,
                amount: state?.amount ?? 0,
                mode:
                  state?.fee_payment_mode === "Cash"
                    ? "Offline"
                    : state?.mode ?? "",
                fee_payment_mode:
                  state?.fee_payment_mode === "Cash"
                    ? "By Cash"
                    : state?.fee_payment_mode,
                fee_payment_amount: state?.fee_payment_amount ?? 0,
                transaction_date: yearMonthDay(state.transaction_date) ?? "",
                fee_bank_name: state?.fee_bank_name ?? "",
                fee_bank_holder: state?.fee_bank_holder ?? "",
                fee_utr_reference: state?.fee_utr_reference ?? "",
                pay_remain: state.pay_remain,
              },
            })
              .then(() => {
                setDisabled((pre) => !pre);
                onClose();
              })
              .catch({});
          }
        }
      }
    } else {
      setErrorField(validation);
    }
  };
  // console.info("applicant", install);

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("collect_fees")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <div className={style.admission_fees_row}>
              <h6>{t("applicable_amount")} :</h6>
              <h6>
                {t("rs_only")} {admission?.applicable_card?.applicable_fee ?? 0}
              </h6>
            </div>
            <div className={style.admission_fees_row}>
              <h6>
                {t(
                  !state.pay_remain
                    ? "remaining_fees"
                    : "amount_remain_installment"
                )}{" "}
                :
              </h6>
              <h6>
                {t("rs_only")}{" "}
                {state.pay_remain ? totalAmount : install?.remainAmount ?? 0}
              </h6>
            </div>
            {admission?.fee_structure?.total_installments ? (
              +admission?.fee_structure?.total_installments > 1 ? (
                <TileWrapper label="pay_rest_remaining_fees">
                  {paymentCollectType?.map((tile) => (
                    <TileCard
                      key={tile?.id}
                      tile={tile}
                      onTile={onCollectType}
                      activeTile={state.pay_remain}
                    />
                  ))}
                </TileWrapper>
              ) : null
            ) : null}
            <FormInput
              label={t("amount")}
              placeholder={t("amoute_details_collect")}
              name={"amount"}
              value={state.amount}
              type={"tel"}
              onChange={onInputAmount}
              errorShow={errorField["amount"]}
              msg={errorField["rest_issue"]}
            />
            <PaymentModeType
              paymentHeading={t("payment_type_mode")}
              selectLabel={t("mode_placeholder")}
              onClick={onModeChange}
              errorShow={errorField["fee_payment_mode"]}
              defalutValue={state.fee_payment_mode}
              selectedValue={t("please_select_payment_mode")}
              isAuthority="admissionStudentProfileCollectMode"
              customLabelStyle={{
                marginTop: "0",
              }}
            />
            <FormCalendarInput
              label={t("select_date_of_transaction")}
              placeholder={t("please_select_date")}
              name={"transaction_date"}
              value={state.transaction_date}
              onChange={onTransactionDate}
              errorShow={errorField["transaction_date"]}
            />
            {state.fee_payment_mode === "Cash" ? null : (
              <>
                <FormInput
                  label={t("bank_name_collect")}
                  placeholder={t("please_enter_bank_name")}
                  name={"fee_bank_name"}
                  value={state.fee_bank_name}
                  type={"text"}
                  onChange={onInputChange}
                />
                <FormInput
                  label={t("banke_holder_name")}
                  placeholder={t("please_enter_name_of_bank_holder")}
                  name={"fee_bank_holder"}
                  value={state.fee_bank_holder}
                  type={"text"}
                  onChange={onInputChange}
                />
                <FormInput
                  label={t("cheque_dd_neft")}
                  placeholder={t("please_enter_utr_ref_number")}
                  name={"fee_utr_reference"}
                  value={state.fee_utr_reference}
                  type={"text"}
                  onChange={onInputChange}
                />
              </>
            )}
            <CreateButton label="collect" onAction={onCollectFees} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default StudentAdmissionCollectFees;
