import { useEffect, useState } from "react";
import style from "./StudentAdmissionFees.module.css";
import { useTranslation } from "react-i18next";
import AddStudentAdmissionFeesCardScholarshipNumber from "./Menus/AddStudentAdmissionFeesCardScholarshipNumber";
import { useLocation } from "react-router-dom";

const StudentAdmissionFeesCardScholarshipNumber = ({ scholarNumber, rcid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();

  const [step, setStep] = useState("");
  const [state, setState] = useState(scholarNumber ?? "");
  useEffect(() => {
    if (scholarNumber) {
      setState(scholarNumber);
    }
  }, [scholarNumber]);
  const onCloseStep = () => {
    setStep("");
  };
  const onAddScholar = () => {
    if (
      // !isAccess[isAuthorized] &&
      // (addNumberAccess[getQuery.state?.collectAccess] ||
      //   addNumberAccess[getQuery.state?.otherFlow] ||
      //   addNumberAccess[getQuery.state?.scholarNumberEdit])
      editScholarShipControl[getQuery.state?.editGovtNumberContorl]
    )
      setStep(1);
  };

  // console.info("getQuery", getQuery.state);
  return (
    <>
      <div
        className={style.student_admission_count_container}
        onClick={onAddScholar}
      >
        <button className={style.add_scholar_number_btn}>
          {state ? (
            <>
              {t("edit_scholarship_application_no")}
              <span
                style={{
                  color: "#121212",
                }}
              >
                {state}
              </span>
            </>
          ) : (
            t("add_scholarship_application_no_if_applicable")
          )}
        </button>
      </div>
      {step === 1 ? (
        <AddStudentAdmissionFeesCardScholarshipNumber
          onClose={onCloseStep}
          scholarNumber={state}
          setScholarNumber={setState}
          rcid={rcid}
        />
      ) : null}
    </>
  );
};

export default StudentAdmissionFeesCardScholarshipNumber;

// const isAccess = {
//   notAuthorized: true,
// };

// const addNumberAccess = {
//   FINANCE_MANAGER: true,
//   Finance_scholarship: true,
// };

const editScholarShipControl = {
  authorized: true,
};
