import { useState } from "react";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import styleAdmission from "../../AdmissionManager/OngoingAdmission/OngoingAdmission.module.css";
import style from "../../FinanceManager/Income/Income.module.css";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useTranslation } from "react-i18next";
import { useStudentAdmissionFeeCardCollectWithSetoff } from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
const StudentAdmissionCollectFeesType = ({
  onClose,
  admission,
  install,
  onSelectType,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [studentAdmissionFeeCardCollectWithSetoff] =
    useStudentAdmissionFeeCardCollectWithSetoff();
  const onSelect = (val) => {
    if (val === 6) {
      if (
        admission?._id &&
        admission?.student?._id &&
        admission?.setOffPrice &&
        install?.installmentValue
      )
        setDisabled((pre) => !pre);
      studentAdmissionFeeCardCollectWithSetoff({
        rcid: admission?._id,
        sid: admission?.student?._id,
        payment: {
          amount: admission?.setOffPrice,
          mode: "Setoff",
          type: install?.installmentValue,
          fee_payment_mode: "NEFT/RTGS/IMPS",
          fee_payment_amount: admission?.setOffPrice,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      onSelectType(val);
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("collect_fees")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <div
              className={styleAdmission.application_card}
              onClick={() => onSelect(6)}
            >
              <div className={styleAdmission.application_card_title}>
                <img
                  src={`${assestsUserFinanceUrl}/category.svg`}
                  alt="staff profile avatar"
                />
                <h6>{t("set_off_excess_fees")}</h6>
              </div>
              <section className={styleAdmission.application_card_info}>
                <div>
                  {t("applicable_pending_fees")}{" "}
                  {`: ${t("rs")}. ${install?.remainAmount ?? 0}`}
                </div>
                <div>
                  {t("excess_fees")}{" "}
                  {`: ${t("rs")}. ${admission?.setOffPrice ?? 0}`}
                </div>
                <div>
                  {t("set_off_fees")}{" "}
                  {`: ${t("rs")}. ${admission?.setOffPrice ?? 0}`}
                </div>
                <h6>{t("set_off_excess_fees_note")}</h6>
              </section>
            </div>
            <div
              className={styleAdmission.application_card}
              onClick={() => onSelect(2)}
              style={{
                minHeight: "100px",
              }}
            >
              <div className={styleAdmission.application_card_title}>
                <img
                  src={`${assestsUserFinanceUrl}/category.svg`}
                  alt="staff profile avatar"
                />
                <h6>{t("collect_fees")}</h6>
              </div>
              <section className={styleAdmission.application_card_info}>
                <h6>{t("collect_fees_note")}</h6>
              </section>
            </div>
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default StudentAdmissionCollectFeesType;
