import { useState } from "react";
import style from "./StudentAdmissionFees.module.css";
import dayjs from "dayjs";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";
// import StudentAdmissionFeesCardReceipt from "../TransactionReceipt/Admission/StudentAdmissionFeesCardReceipt";
import StudentAdmissionScholarshipCollectFees from "./CollectFees/StudentAdmissionScholarshipCollectFees";
import StudentAdmissionFeesCardInstallmentCardGovToApplicableMenu from "./Menus/StudentAdmissionFeesCardInstallmentCardGovToApplicableMenu";
import StudentAdmissionFeesCardInstallmentCardTransferToApplicable from "./Menus/StudentAdmissionFeesCardInstallmentCardTransferToApplicable";
const StudentAdmissionFeesCardInstallmentCardGov = ({ install, admission }) => {
  const getQuery = useLocation();

  const { t } = useTranslation();
  const [step, setStep] = useState("");
  const onCloseStep = () => {
    setStep("");
  };
  const onEditMenu = () => {
    setStep(1);
  };
  const onCollectFees = () => {
    if (
      buttonAccess[getQuery.state?.govtAccess] &&
      getQuery.state?.collectAccess !== "ADMISSION_ADMIN"
    )
      setStep(2);
  };

  const onMenu = () => {
    setStep(3);
  };

  // console.info("getQuery", admission);
  return (
    <>
      <div
        className={style.student_admission_cash_card}
        style={{
          position: "relative",
        }}
      >
        <section className={style.admission_fees_amount}>
          <h6>
            {install?.installmentValue}{" "}
            {install?.status === "Paid" && (
              <span>
                {dayjs(install?.fee_receipt?.fee_transaction_date).format(
                  "DD-MMM-YY"
                )}
              </span>
            )}
          </h6>
          <h6>
            {t("rs_only")}
            {install?.remainAmount ?? 0}
          </h6>
        </section>
        <section
          className={style.admission_fees_grant}
          style={{
            marginBottom: "0",
          }}
        >
          {install?.status === "Paid" ? (
            <>
              {install?.revert_status ===
              "Government Fees Shifted To Applicable Fees" ? (
                <>
                  <h6>{install?.revert_status ?? ""}</h6>
                  <h6>{"N/A"}</h6>
                </>
              ) : (
                <>
                  <h6>
                    {install?.fee_receipt?.fee_payment_mode === "By Cash"
                      ? "Offline"
                      : install?.fee_receipt?.fee_payment_mode
                      ? install?.fee_receipt?.fee_payment_mode
                      : install?.refund_status ?? ""}
                  </h6>
                  <h6>
                    {install?.fee_receipt?.set_off_status === "Set Off"
                      ? "SetOff"
                      : install?.fee_receipt?.fee_payment_mode === "By Cash"
                      ? "Cash"
                      : install?.fee_receipt?.fee_payment_mode ===
                        "Exempted/Unrecovered"
                      ? "N/A"
                      : "Bank"}
                  </h6>
                </>
              )}
            </>
          ) : (
            <>
              {install?.status === "Receipt Rejected" ? (
                <h6>
                  {`${t("reason")} : `}
                  {install?.fee_receipt?.reason}
                </h6>
              ) : (
                <h6>
                  {t("due_on")}
                  {install?.dueDate}
                </h6>
              )}
            </>
          )}
        </section>
        {
          // install?.status !== "Paid" &&
          // install?.revert_status !==
          //   "Government Fees Shifted To Applicable Fees" &&

          editInstallmentControl[getQuery.state?.editGovtInstallmentControl] ? (
            <img
              src={`${assestsUserFinanceUrl}/menu.svg`}
              alt="menu icon"
              onClick={onEditMenu}
              className={style.edit_icon}
              title="Menu"
              style={{
                cursor: "pointer",
                top: "11%",
                right: "0%",
              }}
            />
          ) : null
        }
        {install?.status === "Paid" ? null : (
          // <StudentAdmissionFeesCardReceipt
          //   rid={install?.fee_receipt?._id}
          //   appId={admission?.appId?._id}
          // />
          <button
            className={style.student_admission_collect_btn}
            style={{
              backgroundColor: "#1d22c9f7",
            }}
            onClick={onCollectFees}
          >
            {t(
              buttonAccess[getQuery.state?.govtAccess] &&
                getQuery.state?.collectAccess !== "ADMISSION_ADMIN"
                ? "scholarship_outs"
                : "unpaid"
            )}
          </button>
        )}
        {install?.fee_receipt?.txn_id ||
        install?.fee_receipt?.fee_utr_reference ? (
          <section className={style.admission_fees_grant}>
            <h6>
              {t("transaction_id")} {":"}
              {install?.fee_receipt?.txn_id ??
                install?.fee_receipt?.fee_utr_reference ??
                ""}
            </h6>
          </section>
        ) : null}
      </div>

      {step === 1 ? (
        <StudentAdmissionFeesCardInstallmentCardGovToApplicableMenu
          onClose={onCloseStep}
          onMenu={onMenu}
          install={install}
          admission={admission}
        />
      ) : step === 2 ? (
        <StudentAdmissionScholarshipCollectFees
          onClose={onCloseStep}
          admission={admission}
          install={install}
        />
      ) : step === 3 ? (
        <StudentAdmissionFeesCardInstallmentCardTransferToApplicable
          onClose={onCloseStep}
          installAmount={install?.remainAmount}
          rid={admission?._id}
        />
      ) : null}
    </>
  );
};

export default StudentAdmissionFeesCardInstallmentCardGov;

const buttonAccess = {
  FINANCE_MANAGER: true,
  Finance_scholarship: true,
};

const editInstallmentControl = {
  authorized: true,
};
