import { useEffect, useState } from "react";
import { useStudentAdmissionFeeCardApplicableRevertToScholarship } from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import FormInput from "../../../Ui/Input/FormInput";
import QvipleLoader from "../../../Loader/QvipleLoader";
import style from "../../FinanceManager/Income/Income.module.css";

const StudentAdmissionFeesCardInstallmentCardTransfer = ({
  installId,
  onClose,
  rid,
  installAmount,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState(0);
  const [errorField, setErrorField] = useState(false);

  const [studentAdmissionFeeCardApplicableRevertToScholarship] =
    useStudentAdmissionFeeCardApplicableRevertToScholarship();

  useEffect(() => {
    if (installAmount) {
      setState(installAmount);
    }
  }, [installAmount]);
  const onRemove = () => {
    if (rid && installId) {
      let validation = requiredField({ query: state }, []);
      if (existingRequiredField(validation) && state <= installAmount) {
        setDisabled((pre) => !pre);
        studentAdmissionFeeCardApplicableRevertToScholarship({
          rid: rid,
          applicableBody: {
            raid: installId,
            amount: +state,
          },
        })
          .then(() => {
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      } else {
        if (state > installAmount)
          setErrorField({
            amount_issue:
              "Amount should be less than or equal to installment Amount",
          });
        else setErrorField(validation);
      }
    }
  };
  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("transfer_to_scholarship_fees")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("amount")}
              placeholder={t("please_enter_amount_paid")}
              name={"amount"}
              value={state}
              type={"tel"}
              onChange={(e) => setState(e.target.value)}
              errorShow={errorField["state"]}
              msg={errorField["amount_issue"]}
            />

            <CreateButton label="confirm" onAction={onRemove} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default StudentAdmissionFeesCardInstallmentCardTransfer;
